// Обновление диаграммы в конвертере
var updateLineChart = ({
    commit,
    state
}, payload) => {
    // Запрос к API
    async function update(id, vs_currencie) {
        let vs_currencie_1 = state.allCoins.find((coin) => coin.id == id).symbol
        let id_2 = state.allCoins.find((coin) => coin.symbol == vs_currencie).id
        commit("CLEAR_CHART")
        try {
            let response = null
            let myJson = null
            // Возвращает 1/стоимость в usd
            if (id === "dollar" && vs_currencie !== "usd") {
                response = await fetch(
                    `https://api.coingecko.com/api/v3/coins/${id_2}/market_chart?vs_currency=usd&days=14`
                );
                myJson = await response.json();

                for (let price of myJson["prices"]) {
                    commit("ADD_CHART_DATA", {
                        labels: price[0],
                        label: `Стоимость ${vs_currencie_1} в ${vs_currencie}`,
                        data: 1 / price[1]
                    })
                }
            } else {
                // Возвращает массив единиц
                if (id === "dollar" && vs_currencie === "usd") {
                    response = await fetch(
                        `https://api.coingecko.com/api/v3/coins/bitcoin/market_chart?vs_currency=btc&days=14`
                    );
                    // Возвращает стоимость в usd
                } else {
                    response = await fetch(
                        `https://api.coingecko.com/api/v3/coins/${id}/market_chart?vs_currency=${vs_currencie}&days=14`
                    );
                }
                myJson = await response.json();
                for (let price of myJson["prices"]) {
                    commit("ADD_CHART_DATA", {
                        labels: price[0],
                        label: `Стоимость ${vs_currencie_1} в ${vs_currencie}`,
                        data: price[1]
                    })
                }
            }
        } catch (e) {
            console.error(e);
        }
        // Завершаем загрузку
        commit("LOADING_CHART", {
            state: true
        })
    }
    // Начинаем загрузку
    commit("LOADING_CHART", {
        state: false
    })
    // Делаем запрос к API
    update(payload.id, payload.vs_currencie)
}
async function getWaletCoins({
    commit,
    state
}) {
    commit("CLEAR_WALLET_CHART")
    for (let id of Object.keys(state.walletCoins)) {
        let response
        try {
            if (id === "dollar") {
                commit("ADD_WALLET_COIN_PRICE", {
                    data: {
                        market_data: {
                            current_price: {
                                usd: 1
                            }
                        },
                        symbol: "usd"
                    },
                    id
                })
            } else {
                response = await fetch(
                    `https://api.coingecko.com/api/v3/coins/${id}?localization=false&tickers=true&market_data=true&community_data=false&developer_data=false&sparkline=false`
                );
                const myJson = await response.json();
                commit("ADD_WALLET_COIN_PRICE", {
                    data: myJson,
                    id
                })
            }

        } catch (error) {
            console.error(error);
        }
    }
    commit("UPDATE_TIME_WALLET_COINS")
    commit("SET_WALLET_CHART")
}
// Получение курсов валют
async function getCoins({
    commit,
    state
}) {
    commit("CLEAR_COINS")
    for (let id of state.currenciesIds) {
        try {
            const response = await fetch(
                `https://api.coingecko.com/api/v3/coins/${id}?localization=false&tickers=true&market_data=true&community_data=false&developer_data=false&sparkline=false`
            );
            const myJson = await response.json();
            commit("ADD_COIN", {
                data: myJson
            })

        } catch (error) {
            console.error(error);
        }
    }
    commit("UPDATE_TIME_ALL_COINS")
}

function addToWallet({
    commit,
    state
}, payload) {
    commit("ADD_TO_WALLET", {
        id: payload.id,
        amount: Number(payload.amount)
    })
    getCoins({
        commit,
        state
    })
}

export default {
    updateLineChart,
    getCoins,
    getWaletCoins,
    addToWallet,
}