// Время получения курса всех доступных валют
var UPDATE_TIME_ALL_COINS = (state) => {
  state.ratesWereUpdatedAt = `${new Date().getHours()}:${new Date().getMinutes()}:${new Date().getSeconds()}`
}

/* 
                  КОНВЕРТЕР 
*/

// Вкыл Выкл загрузки диаграммы конвертера
var LOADING_CHART = (state, payload) => {
  state.loaded = payload.state
}

// Очистка диаграммы конвертера
var CLEAR_CHART = (state) => {
  state.chartdata.labels = []
  state.chartdata.datasets[0].data = []
}

// Заполнение диаграммы конвертера
var ADD_CHART_DATA = (state, payload) => {
  state.chartdata.labels.push(payload.labels)
  state.chartdata.datasets[0].label = payload.label
  state.chartdata.datasets[0].data.push(payload.data)
}
// Очистка курсов 
var CLEAR_COINS = (state) => {
  state.allCoins = [{
    id: "dollar",
    symbol: "usd",
    price: 1,
  }, ]
}

// Заполнение курсов 
var ADD_COIN = (state, payload) => {
  state.allCoins = [
    ...state.allCoins,
    {
      id: payload.data["id"],
      symbol: payload.data["symbol"],
      price: +payload.data["market_data"]["current_price"]["usd"],
    },
  ];
}

/* 
                  КОШЕЛЁК 
*/

// Добавить к обьекту с колличеством валюты ее стоимость в USD
var ADD_WALLET_COIN_PRICE = (state, payload) => {
  let amount = state.walletCoins[payload.id].amount
  let rate = payload.data["market_data"]["current_price"]["usd"]
  state.walletCoins[payload.id].inUsd = amount * rate
  state.walletCoins[payload.id].rate = rate
  SET_WALLET_SUM(state, {
    coinInUsd: amount * rate
  })
}
// Очистка графика при изменении сумм портфеля
var CLEAR_WALLET_CHART = (state) => {
  state.walletChartData = []
  state.walletSum = 0
}
// Время получения курсов для кошелька
var UPDATE_TIME_WALLET_COINS = (state) => {
  state.walletWereUpdatedAt = `${new Date().getHours()}:${new Date().getMinutes()}:${new Date().getSeconds()}`
}

// Расчет суммы кошелька в USD
var SET_WALLET_SUM = (state, payload) => {
  if (payload.coinInUsd) {
    state.walletSum += payload.coinInUsd
  } else {
    state.walletSum = "Ошибка"
  }
}

// Расчет данных для графика в %
var SET_WALLET_CHART = (state) => {
  for (let id of Object.keys(state.walletCoins)) {
    state.walletChartData = [...state.walletChartData,
      {
        label: state.walletCoins[id].symbol,
        value: state.walletCoins[id].inUsd / state.walletSum * 100
      }
    ]
  }
}

var ADD_TO_WALLET = (state, payload) => {
  state.walletCoins[payload.id].amount+=payload.amount
  SET_WALLET_SUM(state, {
    coinInUsd: payload.amount
  })
}
export default {
  UPDATE_TIME_ALL_COINS,
  ADD_CHART_DATA,
  LOADING_CHART,
  CLEAR_CHART,
  ADD_COIN,
  CLEAR_COINS,
  ADD_WALLET_COIN_PRICE,
  UPDATE_TIME_WALLET_COINS,
  SET_WALLET_CHART,
  ADD_TO_WALLET,
  CLEAR_WALLET_CHART
}